* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.Footer-container {
    height: 50%;
    width: 100%;
    background-color: #faece9;
    color: rgba(56, 53, 53, 0.752);
    display: flex;
    padding: 10px 25px;
    align-items: center;
}
.first,
.second,
.third {
    width: 33.33%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: justify;
    text-align: center;
}

.first {
    padding: 0 10%;
}
.Footer-container ul {
    display: block;
}
.Footer-container li:hover {
    color: #000000;
    cursor: pointer;
}
.footer-link {
    text-decoration: none;
    color: rgba(56, 53, 53, 0.752);
}
