* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html,
body {
    width: 100%;
}
nav .logo {
    height: 60%;
    width: 100%;
    justify-content: center;
    display: flex;
    align-items: center;

    font-size: 2rem;
    padding: 20px 0;
}
.logo {
    color: rgba(40, 39, 39, 0.852);
    font-family: satisfy;
    font-size: 2rem;
    padding: 0 10px;
}
.nav-links {
    height: 30%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
nav {
    width: 100%;
    background-color: #e3cccc;
    display: flex;
    height: 6rem;
    flex-direction: column;
}
ul {
    display: flex;
}
ul li {
    list-style: none;
    font-family: nuno-sans-light;
    padding-left: 20px;
    font-size: 1rem;
}
.nav-link {
    color: rgba(56, 53, 53, 0.752);
    text-decoration: none;
}
.nav-link:hover {
    border-bottom: 1px solid #000;
    font-weight: 400;
    color: black;
}
